      h1 Alistamiento de pedidos
    p.mb-5 La preparacin de pedidos es la actividad que acondiciona las mercancas y objetos postales, de acuerdo con caractersticas, clasificacin de los productos y requerimientos de los clientes, para que sean organizados en forma de pedidos. Estos pueden ser enviados al cliente final, al centro de distribucin o a otros puntos de despacho.
      br
      br
      |En general, en el proceso de preparacin de pedidos se desarrollan las siguientes tareas:
    .row.mb-5.justify-content-center
      .col-lg-3
        img(src='@/assets/curso/1.svg', alt='Texto que describa la imagen' style='width: 190px').mb-3.m-auto.hoverScale
        .text-center: p Identificacin del pedido (nota de salida), con todos los datos puntuales del pedido, cliente y destino.
      .col-lg-3
        img(src='@/assets/curso/2.svg', alt='Texto que describa la imagen' style='width: 190px').mb-3.m-auto.hoverScale
        .text-center: p Condiciones logsticas para la entrega, incluyendo el tipo de vehculo requerido.
      .col-lg-3
        img(src='@/assets/curso/3.svg', alt='Texto que describa la imagen' style='width: 190px').mb-3.m-auto.hoverScale
        .text-center: p Informacin sobre la ruta y condiciones relevantes de acceso al lugar.
      .col-lg-3
        img(src='@/assets/curso/4.svg', alt='Texto que describa la imagen' style='width: 190px').mb-3.m-auto.hoverScale
        .text-center: p Asignacin de identificacin (consecutivo) para hacer seguimiento de la ubicacin e informar as al cliente.
    p.mb-5 Tenga en cuenta los siguientes aspectos clave, en el inicio del proceso de alistamiento de pedidos:
    .row.justify-content-center.align-items-center.mb-5
