            p.mb-0 Un producto es el <b>resultado de un conjunto de actividades mutuamente relacionadas o que interactan, las cuales transforman elementos de entrada en resultados</b> (ICONTEC, 2005).  Se puede definir que, para el sector de servicios postales, las entradas son aquellas mercancas y objetos encomendados para enviar o recibir en el territorio nacional o en el exterior, momento en el cual se convierte en un producto.
    p.mb-3 Dicho producto debe recorrer una serie de actividades como: recepcin, clasificacin, consolidacin, embalaje, transporte y entrega, siempre sujeto a verificacin o reporte de inconsistencias.
    p.mb-5 Algunas generalidades, de suma importancia, que se deben tener en cuenta, son:
    ImagenInfografica.color-acento-contenido.dots-1.mb-5
      template(v-slot:imagen)
