        p El tema fsico hace referencia a las variables: peso, volumen, forma y durabilidad, mientras que lo intangible respecta a informacin y comunicacin para cubrir las expectativas del cliente.
      .tarjeta.color-primario.p-3(x="66%" y="55%" numero="4")
        p Las caractersticas de los productos permiten gestionar de una forma ms efectiva los inventarios, almacenamiento y el alistamiento de pedidos.
      .tarjeta.color-primario.p-3(x="72%" y="74%" numero="5")
        p <b>Pesovolumen:</b> son medidas directamente proporcionales a los costos de almacenamiento y transporte.  Necesidades de equipos de manipulacin y transporte.  Clculo de la densidad del producto.
      .tarjeta.color-primario.p-3(x="56%" y="73%" numero="6")
        p <b>Valor-peso:</b> utilizada cuando el valor del producto se expresa en relacin con el peso; diferente de los objetos valiosos como joyas, electrodomsticos, etc.
      .tarjeta.color-primario.p-3(x="27%" y="69%" numero="7")
