      h2 1.2 Planificacin de pedidos 
    p.mb-5 En los centros de distribucin, la fase de preparacin es una de las ms relevantes; aqu se asegura que el producto sea el correcto, que su preparacin y embalaje sean los adecuados para llegar en perfectas condiciones a su destino final.
