            p.mb-0 La preparacin de pedidos es tambin llamada <em>picking</em>, y se define como el proceso mediante el cual se seleccionan y recogen los productos desde las estanteras, pasillos, y dems espacios de la bodega o almacn; con el objetivo de organizar y preparar la mercanca y objetos postales para ser embalados (<em>packing</em>) y enviados a sus respectivos clientes finales.
    p.mb-5 Los principales objetivos en la preparacin de pedidos son reducir recorridos, disminuir tiempos y operaciones, y mantener una rotacin de <em>stock</em> precisa; para lograr eficiencia en este proceso es importante el desarrollo de las siguientes fases:
    figure.mb-5
