              tr
                td.p-4 <b>Requisito:</b> cantidad de pedidos baja, almacn pequeo, distancias cortas, uso de equipos manuales.
              tr
                td.p-4.fw-bold(rowspan='2') Producto a la persona
                td.p-4 Desplazamiento de los pedidos hasta las personas en forma manual o automtica.
