                td.p-4 <b>Requisito:</b> cantidad de pedidos medio-alto, salida del producto en unidades o cajas, distancias medio-largas, abastecimiento con pal, utilizacin de sistemas de gestin informticos (SGA) en la preparacin de pedidos.
